.picker {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  padding: 16px;
  -webkit-justify-content: center;
          justify-content: center;
}

.user {
  width: 200px;
  height: 200px;
  position: relative;
}

.user__avatar {
  max-width: 100%;
  max-height: 100%;
}

.user__info {
  position: absolute;
  width: 100%;
  padding: 8px;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
}

.user__name {
  font-weight: bold;
}

.user-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 30px;
  -webkit-justify-content: center;
          justify-content: center;
}

.app {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
}

.app__content {
  display: -webkit-flex;
  display: flex;
  min-height: 90vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.app__error {
  width: 500px;
  margin: 0 auto;
  text-align: center;
  margin-top: 30px;
  border: 1px solid green;
  padding: 5px;
  background: #eee;
}

* {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-weight: 300;
}

